import { setAuthToken } from './setAuth'
import { payOrderSuccessAction } from '../flux/actions/orderActions'
import config from './config'
const WS_HOST = config.WS_HOST

const socket = {
  status: 'close',
  ws: null,
  initSocket: function (token) {
    if (typeof(window) !== 'object' || !token) return;
    const ws = new WebSocket(`${WS_HOST}?token=${token}`)

    ws.onclose = function(e) {
      console.log('socket close!')
      socket.status = 'close'
    }
    ws.onmessage = function(e) {
      var msg = e.data
      try {
        msg = JSON.parse(msg)
      }catch(err) {
        return
      }
      switch(msg.type) {
        case 'transToken':
          return setAuthToken(msg.content)
        case 'payOrderSuccess':
          return payOrderSuccessAction(msg.content)
        default:
          return
      }
    }
    ws.onerror = function(e) {
      console.error('socket error', e)
    }

    ws.onopen = function(e) {
      console.log('socket open')
      socket.status = 'open'
      setTimeout(function() {
        ws.send(JSON.stringify({
          type: 'transToken',
          target: 'koa'
        }))
      }, 1000*60*15)
    }
    socket.ws = ws
  }
}

export default socket
